import React, { Component } from 'react';
import axios from '../../util/ajax';
import { Link } from 'react-router-dom';
import Header from '../../components/header';
import './bless.css';

class Bless extends Component{
  constructor(props) {
    super(props);

    this.state = {
      title: '',
      id: '',
      tid: '',
      msgList: []
    }
  }

  componentDidMount() {
    axios.post('/api/activity/get-user-activity-list',{
      page: 1,
      page_size: 40
    }).then(res => {
      this.setState({
        loading: false
      });

      if(res.data.status === 'success') {

        this.setState({
          title: res.data.data.list[0].title,
          id: res.data.data.list[0].id,
          tid: res.data.data.list[0].template_id,
        },() => {
          this.getMsg();
        })
      }
    })
  }

  getMsg() {
    axios.post('/api/share/msg-list',{
      activity_id: this.state.id,
      page: 1,
      page_size: 1000
    }).then(res => {
      if(res.data.status === 'success') {
        this.setState({
          msgList: res.data.data.list
        })
      }
    })
  }

  back() {
    this.props.history.push('/home');
  }

  render() {
    return (
      <div className="bless">
        <Header content="宾客祝福" back={this.back.bind(this)}/>
        <Link to={`/H5/${this.state.tid}/${this.state.id}/1`} className="toH5">我的活动 <span className="red">“{this.state.title}”</span></Link>

        <p className="block"></p>
        <ul className="bless-list">
          {
            this.state.msgList.map((item, i) => {
              return(
                <li key={i} className="bless-item">
                  <div className="avatar"></div>
                  <div className="right">
                    <div className="name">{item.name}</div>
                    <p className="date">{item.ctime}</p>
                    <p className="msg">{item.msg}</p>
                  </div>
                </li>
              )
            })
          }
        </ul>
      </div>
    )
  }
}

export default Bless


