import React, { Component } from 'react';
import Toast from '../../util/Toast';
import axios from '../../util/ajax';
import { Link } from 'react-router-dom';
import Header from '../../components/header';
import './css/getOut.css'

class GetOut extends Component{
  constructor(props) {
    super(props);

    this.state = {
      id: '',
      howMoney: '',
      money: '',
      request: false,
      cardList: [],
      cardInfo: {}
    }
  }

  componentDidMount() {
    let id = this.props.match.params.id;
    this.setState({
      id,
    },() => {
      axios.post('/api/withdrawals/get-user-money',{}).then(res => {
        if(res.data.status === 'success') {
          this.setState({
            money: res.data.data.money
          })
        }
      })

      axios.post('/api/withdrawals/get-user-bank-list',{}).then(res => {
        if(res.data.status === 'success') {
          this.setState({
            cardList: res.data.data
          }, () => {
            for(let i = 0; i < this.state.cardList.length; i++) {
              if(id == this.state.cardList[i].bank_cate_id) {
                this.setState({
                  cardInfo: this.state.cardList[i]
                })
              }
            }
          })
        }
      })
    })
  }

  getAll() {
    if(parseInt(this.state.money) > 0) {
      this.setState({
        howMoney: this.state.money
      })
    }
  }

  inputHandle(e) {
    let value = e.tartet.value;
    this.setState({
      howMoney: value,
    })
  }

  getOut() {
    this.props.history.push(`/getoutMsg${this.state.id}/${this.state.howMoney}`);
  }


  render() {
    return(
      <div className="getOut">
        <Header content="提现"/>
        <Link to={`/chooseCard/${this.state.cardInfo.bank_cate_id}`} className="chooseCard">
          <div className="des-wrap">
            <div className="cover" style={{backgroundImage: `url(${this.state.cardInfo.logo})`}}></div>
            <div className="info">
              {/*<div className="title">{this.state.cardInfo.name}</div>*/}
              <div className="title">{this.state.cardInfo.bank_name}</div>
              <div className="type">尾号{this.state.cardInfo.bank_no}</div>
            </div>
          </div>

          <div className="go"></div>
        </Link>

        <div className="box">
          <div className="title">提现金额</div>
          <div className="input-wrap">
            <span>￥</span><input type="text" value={this.state.howMoney} onChange={this.inputHandle.bind(this)}/>
          </div>
          <div className="des">
            <div className="info">可用余额{this.state.money}元</div>
            <div className="getAll red" onClick={this.getAll.bind(this)}>全部提现</div>
          </div>
        </div>

        {
          this.state.request ? (
            <div className="getBtn getBtnDisable">马上提现</div>
          ) : (
            <div className="getBtn getBtnAble" onClick={this.getOut.bind(this)}>马上提现</div>
          )
        }
      </div>
    )
  }
}

export default GetOut


