import React, { Component } from 'react';
import Toast from '../../util/Toast';
import axios from '../../util/ajax';
import { Link } from 'react-router-dom';
import Header from '../../components/header';

class AddCardStep1 extends Component{
  constructor(props) {
    super(props);

    this.state = {
      request: false,
      code: '',
      second: 90,
      phone: '',
      timer: null,
    }
  }

  componentDidMount() {
    axios.post('/api/withdrawals/get-user-money',{}).then(res => {
      if(res.data.status === 'success') {
        this.setState({
          phone: res.data.data.send_phone
        }, () => {
          this.getCode();
        })
      }
    })
  }

  back() {
    this.props.history.push('/addCardStep2')
  }

  done() {
    if(this.state.code) {
      let stepObj = JSON.parse(localStorage.getItem('step'));
      axios.post('/api/withdrawals/bind-bank',{
        step: 3,
        bank_no: stepObj.bank_no,
        name: stepObj.name,
        id_card: stepObj.id_card,
        phone: this.state.phone,
        code: this.state.code
      }).then(res => {
        if(res.data.status === 'success') {
          this.props.history.push('/cards');
        }
      })
    } else {
      Toast({
        type: 'fail',
        msg: '验证码为空'
      })
    }
  }

  clearTimer() {
    this.setState({
      second: 90,
      code: false
    })
    clearInterval(this.state.timer);
  }

  inputHandle(e) {
    let value = e.target.value;
    this.setState({
      code: value
    })
  }

  getCode() {
    axios.post('/api/withdrawals/get-code',{
      type: 4,
      phone: this.state.phone
    }).then(res => {
      if(res.data.status === 'success') {
        Toast({
          type: 'success',
          msg: res.data.msg
        })
        // let second = 90;
        // this.setState({
        //   code: true
        // },() => {
        //   this.state.timer = setInterval(() => {
        //     second--;
        //     if(second <= 0) {
        //       this.clearTimer();
        //     } else {
        //       this.setState({
        //         second: second
        //       })
        //     }
        //   }, 1000);
        // });
      }
    })
  }

  render() {
    return(
      <div className="addCardStep1 addCardStep">
        <Header content="验证手机号" back={this.back.bind(this)}/>

        <p className="step3-des">绑定银行卡需要短信确认，验证码已发送至手机:{`${this.state.phone.substr(0,3)}****${this.state.phone.substr(7,11)}`},请按提示操作。</p>

        <div className="step3-box">
          <input className="input" value={this.state.code} onChange={this.inputHandle.bind(this)} type="text" placeholder="请输入验证码"/>

          {/*{*/}
            {/*this.state.code ? (*/}
              {/*<div className="getCode">{this.state.second}s后重新获取</div>*/}
            {/*) : (*/}
              {/*<div className="getCode" onClick={this.getCode.bind(this)}>获取验证码</div>*/}
            {/*)*/}
          {/*}*/}
        </div>
        {
          this.state.request ? (
            <div className="nextStep nextStepDisable">完成绑定</div>
          ) : (
            <div className="nextStep nextStepAble" onClick={this.done.bind(this)}>完成绑定</div>
          )
        }
      </div>
    )
  }
}

export default AddCardStep1;


