import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from '../../util/ajax';
import Header from '../../components/header';
import './css/chooseCard.css';

class ChooseCard extends Component{
  constructor(props) {
    super(props);

    this.state = {
      id: '',
      list: []
    }
  }

  componentDidMount() {
    let id = this.props.match.params.id;
    if(id) {
      this.setState({
        id
      }, () => {
        this.getData();
      })
    }
  }

  back() {
    this.props.history.push(`/getOut/${this.state.id}`);
  }

  getData() {
    axios.post('/api/withdrawals/get-user-bank-list',{}).then(res => {
      if(res.data.status === 'success') {
        this.setState({
          list: res.data.data
        })
      }
    })
  }

  render() {
    return(
      <div className="chooseCard">
        <Header content="选择银行卡" back={this.back.bind(this)}/>
        <div className="items">
          {
            this.state.list.map((item, i) => {
              return (
                <Link to={`/getOut/${item.bank_cate_id}`} className={`item ${this.state.id == item.id ? 'active' : ''}`}>招商银行<span className="right"></span></Link>
              )
            })
          }
        </div>
      </div>
    )
  }
}

export default ChooseCard;


