import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from '../../util/ajax';
import Toast from '../../util/Toast';
import Header from '../../components/header';
import './css/chooseCard.css';

class getoutMsg extends Component{
  constructor(props) {
    super(props);

    this.state = {
      id: '',
      money: '',
      request: false,
      code: '',
      phone: '',
    }
  }

  componentDidMount() {
    let id = this.props.match.params.id;
    let money = this.props.match.params.money;
    this.setState({
      id,
      money,
    });
    axios.post('/api/withdrawals/get-user-money',{}).then(res => {
      if(res.data.status === 'success') {
        this.setState({
          phone: res.data.data.send_phone
        }, () => {
          this.getCode();
        })
      }
    })
  }

  back() {
    this.props.history.push(`/getOut${this.state.id}`)
  }

  done() {
    if(this.state.code) {
      axios.post('/api/withdrawals/user-withdrawals', {
        money: this.state.money,
        bank_id: this.state.id,
        code: this.state.code,
        send_phone: this.state.phone,
      }).then(res => {
        if(res.data.status === 'success') {
          Toast({
            type: 'success',
            msg: res.data.msg
          });

          this.props.history.push('/cards');
        }
      })
    } else {
      Toast({
        type: 'fail',
        msg: '验证码为空'
      })
    }
  }

  inputHandle(e) {
    let value = e.target.value;
    this.setState({
      code: value
    })
  }

  getCode() {
    axios.post('/api/withdrawals/get-code',{
      type: 5,
      phone: this.state.phone
    }).then(res => {
      if(res.data.status === 'success') {
        Toast({
          type: 'success',
          msg: res.data.msg
        })
      }
    })
  }

  render() {
    return(
      <div className="addCardStep1 addCardStep">
        <Header content="验证手机号" back={this.back.bind(this)}/>

        <p className="step3-des">验证码已发送至手机:{`${this.state.phone.substr(0,3)}****${this.state.phone.substr(7,11)}`},请按提示操作。</p>

        <div className="step3-box">
          <input className="input" value={this.state.code} onChange={this.inputHandle.bind(this)} type="text" placeholder="请输入验证码"/>
        </div>
        {
          this.state.request ? (
            <div className="nextStep nextStepDisable">完成提现</div>
          ) : (
            <div className="nextStep nextStepAble" onClick={this.done.bind(this)}>完成提现</div>
          )
        }
      </div>
    )
  }
}

export default getoutMsg


